//   URL,     API
const base_url = 'https://api.thecatapi.com/v1'; //  URL API
const end_point = '/breeds'; //       
const end_point_two = '/images/search'; //      
const api_key = '3a9bcc0c5da34369acb142820232808'; //  API   //6f3bf7a06a84400a8c1181835232108

//       
export function fetchBreeds() {
  return fetch(`${base_url}${end_point}?api_key=${api_key}`).then(response => {
    console.log(response); //  '     
    if (!response.ok) {
      throw new Error(response.statusText); //   
    }
    return response.json(); //     JSON
  });
}

//        
export function fetchCatByBreed(breedId) {
  return fetch(
    `${base_url}${end_point_two}?api_key=${api_key}&breed_ids=${breedId}`
  ).then(response => {
    console.log(33333);
    console.log({ response }); //  '     
    if (!response.ok) {
      throw new Error(response.statusText); //   
    }
    return response.json(); //     JSON
  });
}
