//     
import { fetchBreeds, fetchCatByBreed } from './cat-api'; //    cat-api.js
import Notiflix from 'notiflix'; //    
import SlimSelect from 'slim-select'; //     
import 'slim-select/dist/slimselect.css'; //     
import './style.css'; //     styles.css

//   
const elements = {
  select: document.querySelector('.breed-select'), //   ()
  loader: document.querySelector('.loader'), // 
  error: document.querySelector('.error'), //   
  catInfo: document.querySelector('.cat-info'), //   
};

//   
const { select, loader, error, catInfo } = elements;

//     
select.classList.add('is-hidden');
error.classList.add('is-hidden');
loader.classList.remove('is-hidden');

//      
let breedOptions = [];

//        
fetchBreeds()
  .then(data => {
    breedOptions = data.map(breed => ({ value: breed.id, text: breed.name }));
    initializeSelect();
    select.classList.remove('is-hidden'); //  
    loader.classList.add('is-hidden'); //  
  })
  .catch(onErrorFetch); //     

//   
function initializeSelect() {
  new SlimSelect({
    select: select,
    data: breedOptions,
  });
  select.addEventListener('change', onSelectBreedCats); //      
}

//     
function onErrorFetch() {
  loader.classList.add('is-hidden'); //  
  select.classList.remove('is-hidden'); //  
  Notiflix.Notify.failure(
    'Oops! Something went wrong! Try reloading the page!'
  ); //   
}

//     
function onSelectBreedCats(evt) {
  loader.classList.remove('is-hidden'); //  
  select.classList.add('is-hidden'); //  
  catInfo.classList.add('is-hidden'); //    

  //      
  const breedId = evt.target.value;
  fetchCatByBreed(breedId)
    .then(data => {
      console.log({ data });
      const { url, id, width } = data[0];
      const x = fetch(`https://api.thecatapi.com/v1/images/${id}`).then(res =>
        console.log({ res })
      );

      //  
      catInfo.innerHTML = `
						<div class="img">
							 <img src="${url}" id="${id}" width="500"/>
						</div>
					  `;
      catInfo.classList.remove('is-hidden'); //    
    })
    .catch(onErrorFetch) //     
    .finally(() => {
      loader.classList.add('is-hidden'); //  
      select.classList.remove('is-hidden'); //  
    });
}
//        ID 
function displayCatInfoById(imageId, breeds) {
  const selectedBreed = breeds[0]; //      breeds (         )
  const catInfo = document.querySelector('.info'); //  DOM      
  const { id, url } = selectedBreed; //  ID  URL 

  //  ID   ID    ,   
  if (id === imageId) {
    catInfo.innerHTML = `
		 <div class="info">
			<h1>${selectedBreed.name}</h1>
			<p>${selectedBreed.description}</p>
			<p><i><b>Life_span: </b></i>${selectedBreed.life_span} years</p>
			<p><i><b>Temperament:</b></i> ${selectedBreed.temperament}</p>
		 </div>
	  `;

    catInfo.classList.remove('is-hidden'); //     
  } else {
    catInfo.classList.add('is-hidden'); //     ,  ID  
  }
}
